    }

    .article2 {
        .row {
            ul { display:flex; flex-wrap:wrap; padding:30px 0;
                li { width:33.33%; padding:20px;
                    a { display:flex;
                        img { width:50%; margin-right:10px; border-radius:10px }
                        div { align-self:center; }
                    }
                }
            }
            .lifeStyle {
                h2 { font-size:20px }
                ul { display:flex;
                    li { width:33.33%; padding:0 10px ;
                        img { width:100%; border-radius:10px }
                    }
                }
            }
        }
    }

    .article3 {
        background:#000; color:#fff; padding:100px 0px;
        .row { display:flex; 
            div { width:52%;
                img { width:100%; padding:10px 10px 0px; 
                    border-radius:30px 30px 0px 0px; 
                    vertical-align:middle }
                p { background:#999; margin:0px 10px; 
                    padding:20px;  
                    border-radius:0px 0px 30px 30px }
                &.right {
                    width:48%;
                    display:flex; flex-direction:column; justify-content:space-between;
                    div { width:100%;   }
                .rightFst { display:flex; flex-direction:row; }
                }
            }
        }
    }

}