.exwrap{
    background:#fff;  min-height:300px; padding-top:30px;
    h2{
        padding:5px 0; color:green; font-size:30px;
    }
    .infowrap{
        display:flex;
        .proinfo{
            padding:10px 1.5px;; width:20%;
            img{width:100%; border-radius:10px;}
            p{padding:5px 0;}
        }
    }
}
.timewrap{
    background:#fff; min-height:300px;
    .timeinfo{
        display:flex; flex:1 1 1;
        div{
            width:33.33%; display:flex; flex:1 1; padding:10px 5px;
            img{width:50%; border-radius:10px;}
            div{ display:grid; align-items:bottom;
                p{padding:5px;}
                strong{font-size:20px; padding:5px;}
            }
        }
        
    }
}